/** DAY CONTAINERS **/

.today {
  background-color: #f5f5f5;
}

.dayOfWeek {
  -ms-flex: 1 1;
      flex: 1 1;
  position: relative;
  border-right: 1px solid #e0e0e0;
}

.selectedDay {
  color: #4285f4;
  font-weight: 400;
}

/** DAY CONTENTS **/

.otherMonthDay .dateLabel {
  opacity: 0.3;
}

.otherMonthDay .events {
  opacity: 0.5;
}

.dateLabel {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 25px;
  height: 25px;
  font-weight: 200;
  font-size: 14px;
  margin-top: 5px;
}

.today .dateLabel {
  margin-left: 5px;
  background-color: #4285f4;
  color: white;
  border-radius: 15px;
}

.dayLabel {
  display: block;
  font-weight: 200;
  font-size: 14px;
  height: auto;
  margin-top: 0px;
  padding-top: 3px;
  margin-left: 5px;
  color: #757575;
  border-right: 1px solid #e0e0e0;
}

.events {
  /* absolutely position to prevent height growth of calendar row */
  position: absolute;
  top: 15px;
  left: 0;
  right: 0;
  padding: 0;
  list-style-type: none;
  white-space: nowrap;
}

.eventLabel {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  font-size: 10px;
  font-weight: 200;
  margin: 5px 5px 10px 5px;
  cursor: pointer;
}



