/** CALENDAR CONTAINER **/

#calendarContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
  max-width: 700px;
  max-height: 700px;
  border-radius: 5px;
  -webkit-box-shadow: 0px 2px 10px lightgrey;
          box-shadow: 0px 2px 10px lightgrey;
  padding: 40px;
}

/** CALENDAR HEADER **/

#calendarHeader {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 30px;
}

#calendarYear {
  font-size: 30px;
  font-weight: 300;
}

#calendarMonth {
  font-size: 30px;
  font-weight: 500;
  margin-right: 7px;
}

#buttonGroup button {
  cursor: pointer;
  margin-left: 3px;
  padding: 0px 10px;
  height: 40px;
  background-color: transparent;
  outline: none;
  border: solid 1px #e0e0e0;
}

#buttonGroup button:active {
  background-color: #75B8FF;
  color: white;
}

/** CALENDAR WRAPPER & WEEK CONTAINERS **/

#calendaWrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-direction: column;
      flex-direction: column;
  border-left: 1px solid #e0e0e0;
  border-top: 1px solid #e0e0e0;
}

.weekContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  border-bottom: 1px solid #e0e0e0;
}

#daysOfWeek {
  display: -ms-flexbox;
  display: flex;
}

#daysOfWeek > .dayLabel {
  -ms-flex: 1 1;
      flex: 1 1;
}



